import React from 'react'
import { toast } from "react-toastify";
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader'
import { FiMinusCircle } from 'react-icons/fi'
import cssVar from 'views/mobileView/styles'
import db from 'libs/db'
import SearchScreen from 'views/mobileView/components/pages/SearchScreen';
import { minInvestment } from 'views/mobileView/components/atoms/MinInvestment'
import AMCIcon from 'views/mobileView/components/atoms/AMCIcon';

const BuildMintbox = (props) => {

    const [selectedETFs, setSelectedETFs] = React.useState([]);

    React.useEffect(async () => {
        await getSelectedETFs();
    }, []);

    React.useEffect(() => {
        console.log(selectedETFs)
        saveData(selectedETFs)
    }, [selectedETFs]);

    const getSelectedETFs = async () => {
        try {
            await db.get(db.model.createMintbox).then((u) => {
                if (u?.selectedETFs) {
                    setSelectedETFs(u.selectedETFs)
                }
            })
        } catch (error) {
            toast.error(error);
        }
    };

    const saveData = async (arr) => {
        const dataFinal = {
            selectedETFs: arr
        };
        await db.get(db.model.createMintbox).then(async (u) => {
            if (u) { Object.assign(u, dataFinal) }
            else { u = dataFinal }
            await db.set(db.model.createMintbox,JSON.stringify(u));
        })
    }

    const onSearchItemClick = (item) => {
        if(selectedETFs.length === 10){
            toast.error("Maximum 10 ETFs allowed");
            return;
        }
        if (selectedETFs.findIndex(f => f.Symbol === item.Symbol) === -1) {
            setSelectedETFs([...selectedETFs, item])
        }
    }
    const Footer = () => {
        const initializeEqualWeights = async (arr) => {
            const unlockedETFsCount = arr.filter(f => !f.isLocked).length;
            if (unlockedETFsCount === 0) {
                return
            }
            const lockedWeight = arr.reduce((sum, f) => {
                if (f.isLocked) {
                    return sum + f.allocation
                }
                return sum
            }, 0)
            console.log("lockedWeight", lockedWeight)
            const remainingWeight = 100 - lockedWeight;
            const firstNotLockedIndex = arr.findIndex((f) => !f.isLocked)
            console.log('unlockedETFsCount:', unlockedETFsCount)
            console.log('remainingWeight:', remainingWeight)
            let newArr = arr.map((fund, i) => {
                if (fund.isLocked) {
                    return fund;
                }
                if (i === firstNotLockedIndex) { return { ...fund, allocation: Math.floor(remainingWeight / unlockedETFsCount) + remainingWeight % unlockedETFsCount } }
                else return { ...fund, allocation: Math.floor(remainingWeight / unlockedETFsCount) }
            })
            saveData(newArr)
        }

        const onNextPress = async () => {
            if (selectedETFs.length >= 2) {
                await initializeEqualWeights(selectedETFs);
                props.history.push('/CreateMintbox/define-weights')
            }
        }

        return (
            <>
                <div className="bodyTextLight" style={{ position: "fixed", bottom: '70px', width: "100%", textAlign: "center", borderTop: "1px solid "+cssVar.getPropertyValue('--light'), padding: "2% 4%", backgroundColor: cssVar.getPropertyValue('--white') }}>
                    Min investment <span className='header2dark'>₹ {Number(minInvestment(selectedETFs, 1)).toFixed(2)}</span>
                </div>
                <div className='footer'>
                    <div className='bodyTextDark' style={{ flex: 1, paddingRight: "2%" }}>
                        Define ETF weightages on the next screen
                    </div>
                    <div style={{ flex: 1 }}>
                        <button
                            className='solidButton'
                            style={{ opacity: selectedETFs.length >= 2 ? 1 : 0.4 }}
                            onClick={() => { onNextPress() }}
                        >
                            Confirm Selection
                        </button>
                    </div>
                </div>
            </>
        )
    }

    const DisplaySelectedETFs = () => {
        const onDelete = (id) => {
            setSelectedETFs(selectedETFs.filter(f => f.Symbol !== id));
        }
        return (
            <>
                <div className="header2dark" style={{ marginLeft : "4%", marginTop:"2%"}}>{selectedETFs.length} ETFs selected</div>
                <div className="smallTextLight" style={{ marginLeft: "4%"}}>Min 2 | Max 10</div>
                {selectedETFs.map((fund) => {
                    return (
                        <div style={{ padding: "4%", borderBottom: "1px solid "+cssVar.getPropertyValue('--light') }} key={fund.Symbol}>
                            <div style={{ display: "flex", gap: "4%" }}>
                                <AMCIcon amcName={fund.AMCName} height='24px' width='24px' />
                                <div style={{ flex: "1" }}>
                                    <div className='header2dark'>{fund.ETFName}</div>
                                    <div className='smallTextLight'>NAV ₹ {Number(fund.NAV).toFixed(2)}</div>
                                    <div className='header3dark secondaryGreen' style={{marginTop:'2%'}} onClick={()=>props.history.push("/ETF-detail/"+fund.Symbol)}>View details</div>
                                </div>
                                <FiMinusCircle size={32} style={{ backgroundColor: "#FFE7E5", color: '#FF8080', borderRadius: "50%", margin: "auto 0" }} onClick={() => onDelete(fund.Symbol)} />
                            </div>
                        </div>
                    )
                })}
                {selectedETFs.length === 1 && <div style={{ margin: "4%", fontSize: "14px", fontWeight: "400", color: "#333333" }}>Add at least <b>one more</b> ETF to build your own mintbox</div>}
            </>
        )
    }

    return (
        <div className="mainContainer">
            <MainHeader
                header="Build your own mintbox"
                navigation={props.history}
            />
            <div className="dataContainer px-0" style={{paddingBottom:"110px", paddingTop:"2%" }}>
                <div>
                    <SearchScreen 
                        searchText ='Search by ETF name'
                        navigation={props.history}
                        type={1}
                        showPlusIcon={true}
                        plusIconAction={(item)=>onSearchItemClick(item)}
                        showViewDetails={true}
                        viewDetailsAction={(item)=>props.history.push("/ETF-detail/"+item.Symbol)}
                    /> 
                </div>
                {selectedETFs.length === 0 ?
                    <div className='bodyTextDark' style={{ margin: "4%", fontSize:"16px" }}>Search and add ETFs to start building!</div>
                    :
                    <>
                        <DisplaySelectedETFs />
                        <Footer />
                    </>
                }
            </div>
        </div>
    )
}

export default BuildMintbox
